* {
  font-family: sans-serif;
}

.hidden {
  opacity: 0;
}

/* Task 1 */

.todo-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.todo-box {
  position: relative;
  display: flex;
}

.todo-error {
  position: absolute;
  top: -8px;
  left: -120px;

  color: #c82333;
}

.todo-input {
  padding: 8px;
  margin-bottom: 15px;

  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
}

.todo-button {
  padding: 8px;
  color: white;
  background-color: rgb(79, 79, 246);
  border-radius: 4px;
  border: none;
  cursor: pointer;
}

.todo-button:hover {
  background-color: rgb(62, 62, 255);
}

.todo-list {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.todo-item {
  display: flex;
  align-items: center;
  padding: 10px;
  margin-bottom: 5px;
  width: 450px;

  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.completed p {
  text-decoration: line-through;
  color: gray;
}

.todo-checkbox {
  margin: 0 10px 0 auto;
}

.delete-button {
  background-color: #dc3545;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 5px 10px;
  cursor: pointer;
}

.delete-button:hover {
  background-color: #c82333;
}

/* Task 2 */

.audit {
  padding: 50px;
  background-color: antiquewhite;
}

.audit-container {
  position: relative;
}

.audit-form {
  position: relative;

  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  width: 100%;
  height: 250px;
  max-width: 400px;

  background: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.audit-login {
  padding: 8px;
  margin: 10px 0 15px 0;

  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 10px;
}

.audit-password {
  padding: 8px;
  margin: 15px 0 10px 0;

  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 10px;
}

.audit-couple {
  display: flex;
  gap: 20px;
  margin: 60px auto 0 auto;
}

.audit-button {
  display: flex;
  justify-content: center;

  width: 100px;
  padding: 5px 10px;
  border: none;
  border-radius: 4px;
  background-color: rgb(228, 239, 79);
  cursor: pointer;
}

.audit-button:hover {
  background-color: rgb(216, 230, 31);
}

.audit-error {
  position: absolute;
  top: 175px;
  left: 115px;

  color: #c82333;
}

.audit-correct {
  position: absolute;
  top: 175px;
  left: 140px;

  color: #56d230;
}
